import * as React from 'react'
import { useEffect, useReducer } from 'react'
import {
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Button, createStyles, makeStyles, Theme } from '@material-ui/core'
import { Access } from '../../core/ticket-details/toggle-button'
import { TicketDetails } from '../../core/ticket-details/ticket-details'
import {
  blockAccessButtonInitialState,
  toggleAccessButtonReducer,
} from '../../core/ticket-details/toggle-access-button.reducer'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginLeft: theme.spacing(1),
    },
  }),
)

export const ToggleAccessButton = () => {
  const classes = useStyles()
  const t = useTranslate()
  const notify = useNotify()
  const { loading } = useShowContext()
  const [{ type, text, mutateQuery }, dispatch] = useReducer(
    toggleAccessButtonReducer,
    blockAccessButtonInitialState,
  )
  const ticketDetails = useRecordContext<TicketDetails>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  const isActive = (td: TicketDetails) =>
    td.tickets.some((ticket) => ticket.accessUsings.some((a) => a.active))

  useEffect(() => {
    if (isActive(ticketDetails)) {
      dispatch({ type: Access.UNBLOCK })
    } else {
      dispatch({ type: Access.BLOCK })
    }
  }, [ticketDetails])

  const onClick = async () => {
    try {
      const query = mutateQuery?.(ticketDetails)
      await mutate(query, { returnPromise: true })
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <Button
      disabled={type === Access.DISABLED || loading || mutationLoading}
      className={classes.button}
      variant="contained"
      color="secondary"
      onClick={onClick}
    >
      {t(text)}
    </Button>
  )
}
