import * as React from 'react'
import {
  DateField,
  Labeled,
  TextField,
  TextFieldProps,
  useRecordContext,
  useTranslate,
} from 'react-admin'
import {
  Box,
  Chip,
  createStyles,
  makeStyles,
  Paper,
  Theme,
} from '@material-ui/core'
import { mapFromTicketType } from '../../core/ticket-details/ticket-details-values.mapper'
import { TicketDetails } from '../../core/ticket-details/ticket-details'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    ticketBox: {
      display: 'flex',
      flexDirection: 'column',
      padding: theme.spacing(1),
      marginLeft: theme.spacing(1),
    },
    ticketField: {
      padding: theme.spacing(0),
      margin: theme.spacing(0),
      marginBottom: theme.spacing(0.5),
    },
  }),
)

export const TicketDetailsTicketsField = ({ label }: TextFieldProps) => {
  const classes = useStyles()
  const t = useTranslate()
  const record = useRecordContext<TicketDetails>()
  const mappedRecords = (record.tickets || []).map(
    ({ id, ticketType, createdAt, ticketCode }) => ({
      id,
      ticketCode,
      ticketType: mapFromTicketType(ticketType),
      createdAt,
    }),
  )
  const fields = mappedRecords.map((mappedRecord) => (
    <Paper
      key={mappedRecord.id}
      variant="elevation"
      className={classes.ticketBox}
    >
      <Labeled
        className={classes.ticketField}
        label="resources.ticket-details.fields.tickets.fields.id"
      >
        <TextField
          className={classes.ticketField}
          record={mappedRecord}
          source="id"
        />
      </Labeled>
      <Labeled
        className={classes.ticketField}
        label="resources.ticket-details.fields.tickets.fields.ticketCode"
      >
        <TextField
          className={classes.ticketField}
          record={mappedRecord}
          source="ticketCode"
        />
      </Labeled>
      <Labeled
        className={classes.ticketField}
        label="resources.ticket-details.fields.tickets.fields.ticketType"
      >
        <Chip
          label={t(mappedRecord.ticketType)}
          className={classes.ticketField}
        />
      </Labeled>
      <Labeled
        className={classes.ticketField}
        label="resources.ticket-details.fields.tickets.fields.createdAt"
      >
        <DateField
          className={classes.ticketField}
          record={mappedRecord}
          source="createdAt"
          showTime
        />
      </Labeled>
    </Paper>
  ))
  return (
    <Labeled label={label}>
      <Box display="flex" flexWrap="wrap" flexDirection="row">
        {fields}
      </Box>
    </Labeled>
  )
}
