import * as React from 'react'
import { useEffect, useReducer } from 'react'
import {
  sanitizeFetchType,
  UPDATE,
  UpdateParams,
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Button, createStyles, makeStyles, Theme } from '@material-ui/core'
import { TicketDetails } from '../../core/ticket-details/ticket-details'
import { BookingUpdateRequest } from '../../core/bookings/booking-update.request'
import { BookingDisabledOption } from '../../core/enum/BookingDisabledOption'
import { ResourceName } from '../../core/ResourceName'
import {
  blockTicketButtonInitialState,
  toggleTicketButtonReducer,
} from '../../core/ticket-details/toggle-ticket-button.reducer'
import { Access } from '../../core/ticket-details/toggle-button'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginLeft: theme.spacing(1),
    },
  }),
)

export const ToggleTicketButton = () => {
  const classes = useStyles()
  const t = useTranslate()
  const notify = useNotify()
  const { loading } = useShowContext()
  const [{ type, text, mutateParam }, dispatch] = useReducer(
    toggleTicketButtonReducer,
    blockTicketButtonInitialState,
  )
  const { id: bookingId, bookingDisabled } = useRecordContext<TicketDetails>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  useEffect(() => {
    if (bookingDisabled === null) {
      dispatch({ type: Access.BLOCK })
    } else if (bookingDisabled === BookingDisabledOption.ADMIN) {
      dispatch({ type: Access.UNBLOCK })
    } else {
      dispatch({ type: Access.DISABLED })
    }
  }, [bookingDisabled])

  const onClick = async () => {
    try {
      await mutate(
        {
          type: sanitizeFetchType(UPDATE),
          resource: ResourceName.BOOKINGS,
          payload: {
            data: { id: bookingId, disabled: mutateParam },
          } as UpdateParams<BookingUpdateRequest>,
        },
        { returnPromise: true },
      )
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <Button
      disabled={type === Access.DISABLED || loading || mutationLoading}
      className={classes.button}
      variant="contained"
      color="secondary"
      onClick={onClick}
    >
      {t(text)}
    </Button>
  )
}
