import { CustomerDto } from '../dto/customer.dto'
import { EventDto } from '../dto/event.dto'
import { TicketDto } from '../dto/ticket.dto'
import { TicketInfo } from './ticket-info'
import { BookingDto } from '../dto/booking.dto'
import { toMapById } from '../common/mapper.utils'

export const mapToTicketBaseInfo = (
  { firstname, lastname, pesel }: CustomerDto,
  { name: eventName }: EventDto,
  { bookingId, ticketCode, ticketType }: TicketDto,
): TicketInfo => ({
  id: bookingId,
  firstname,
  lastname,
  pesel,
  eventName,
  ticketCode,
  ticketType,
})

export const mapToTicketBaseInfoList = (
  bookings: BookingDto[],
  customers: CustomerDto[],
  events: EventDto[],
  tickets: TicketDto[],
): TicketInfo[] => {
  const bookingById = toMapById(bookings)
  const customerById = toMapById(customers)
  const eventById = toMapById(events)
  return tickets.map((ticket) => {
    const { customerId, eventId } = bookingById.get(
      ticket.bookingId,
    ) as BookingDto
    const customer = customerById.get(customerId) as CustomerDto
    const event = eventById.get(eventId) as EventDto
    return mapToTicketBaseInfo(customer, event, ticket)
  })
}
