import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Filter,
  FilterProps,
  List,
  Pagination,
  SelectInput,
  TextInput,
  ListProps,
  useTranslate,
  useLocale,
} from 'react-admin'
import { MobilePostPagination } from '../common/Pagination'
import { DateFilterInput } from './DateFilterInput'
import { LogGrid } from './LogGrid'

interface LogListProps extends ListProps {
  readonly compactMode?: boolean
  readonly noBooking?: boolean
}

const ListFilters = (props: Omit<FilterProps, 'children'>) => {
  const t = useTranslate()
  return (
    <Filter {...props}>
      <TextInput source="ticketCode" alwaysOn />
      <SelectInput
        source="deviceName"
        choices={[
          {
            id: 'Czytnik',
            name: t('resources.logs.fields.deviceNameChoices.reader'),
          },
          {
            id: 'Przej\u015Bcie',
            name: t('resources.logs.fields.deviceNameChoices.gateway'),
          },
        ]}
      />
      <DateFilterInput source="date" key="date" />
      <TextInput source="bookingId" />
      <TextInput source="event" />
    </Filter>
  )
}

export const LogList = ({
  compactMode = false,
  noBooking = false,
  ...props
}: LogListProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const locale = useLocale()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={
        smallScreen ? (
          <MobilePostPagination />
        ) : (
          <Pagination rowsPerPageOptions={[20, 50, 100]} />
        )
      }
      filters={<ListFilters />}
      filterDefaultValues={{ locale }}
      sort={{ field: 'date', order: 'DESC' }}
      {...props}
    >
      <LogGrid compactMode={compactMode} noBooking={noBooking} />
    </List>
  )
}
