import * as React from 'react'
import {
  Datagrid,
  DateField,
  Filter,
  FilterProps,
  List,
  ListProps,
  TextField,
  TextInput,
  useListContext,
} from 'react-admin'
import { ResourceName } from '../../../core/ResourceName'

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

const OverallViewGrid = () => {
  const { loading } = useListContext()
  // TODO: header doesn't change color when theme value is switched
  //  the reason is the need to change the theme at <Admin> level - not at <Layout> level
  //  the solution is here - https://stackoverflow.com/a/63967145 -> after that customReducer needs to be removed
  return (
    <Datagrid
      size="medium"
      loaded={!loading}
      rowClick={(id) => `${ResourceName.STATS_OVERALL_VIEW}/${id}/show`}
    >
      <TextField source="id" sortable={false} />
      <TextField source="name" sortable={false} />
      <DateField source="dateStart" sortable={false} showTime />
      <DateField source="dateEnd" sortable={false} showTime />
    </Datagrid>
  )
}

export const OverallViewList = (props: ListProps) => (
  <List {...props} actions={false} pagination={false} filters={<ListFilters />}>
    <OverallViewGrid />
  </List>
)
