import { DataProvider, GetOneParams, GetOneResult } from 'react-admin'
import {
  ACCESS_USINGS,
  BOOKINGS_URL,
  CUSTOMERS_URL,
  EVENTS_URL,
  TICKETS_URL,
  VARIANTS_URL,
} from '../api-urls'
import { get, getByConditions } from '../common/fetch.utils'
import { BookingDto } from '../dto/booking.dto'
import { TicketDto } from '../dto/ticket.dto'
import { CustomerDto } from '../dto/customer.dto'
import { EventDto } from '../dto/event.dto'
import { buildFilterByIdsCondition } from '../common/condition.builder'
import { mapToTicketDetails } from './ticket-details.mapper'
import { VariantDto } from '../dto/variant.dto'
import { TicketDetails } from './ticket-details'
import { toUniqueValues } from '../common/mapper.utils'
import { AccessUsingDto } from '../dto/access-using.dto'

const fetchTicketsWithAccessUsings = async (ticketIds: number[]) => {
  const tickets = await getByConditions<TicketDto>(
    TICKETS_URL,
    buildFilterByIdsCondition(ticketIds),
  )
  const accessUsingIds = tickets.flatMap((t) => t.accessUsingsIds)
  const accessUsings = accessUsingIds.length
    ? await getByConditions<AccessUsingDto>(
        ACCESS_USINGS,
        buildFilterByIdsCondition(toUniqueValues(accessUsingIds)),
      )
    : []
  return {
    tickets,
    accessUsings,
  }
}

const provider = {
  getOne: async (
    resource: string,
    { id: bookingId }: GetOneParams,
  ): Promise<GetOneResult<TicketDetails>> => {
    const booking = await get<BookingDto>(BOOKINGS_URL, `/${bookingId}`)
    const { customerId, eventId, variantId } = booking
    const pendingCustomer = get<CustomerDto>(CUSTOMERS_URL, `/${customerId}`)
    const pendingEvent = get<EventDto>(EVENTS_URL, `/${eventId}`)
    const pendingVariant = get<VariantDto>(VARIANTS_URL, `/${variantId}`)
    const pendingTicketsWithAccessUsings = fetchTicketsWithAccessUsings(
      booking.ticketsIds,
    )

    const [customer, event, variant, { tickets, accessUsings }] =
      await Promise.all([
        pendingCustomer,
        pendingEvent,
        pendingVariant,
        pendingTicketsWithAccessUsings,
      ])

    return {
      data: mapToTicketDetails(
        booking,
        customer,
        event,
        variant,
        tickets,
        accessUsings,
      ),
    }
  },
} as DataProvider

export default provider
