import {
  CREATE,
  CreateParams,
  sanitizeFetchType,
  UPDATE_MANY,
  UpdateManyParams,
} from 'react-admin'
import { TicketDetails } from './ticket-details'
import { ResourceName } from '../ResourceName'
import { AccessUsingsCreateParams } from '../access-usings/access-usings-create.params'
import { Access, Action, ToggleButtonState } from './toggle-button'

type Mutation = {
  type: string
  resource: string
  // eslint-disable-next-line @typescript-eslint/ban-types
  payload?: object
}

const blockMutation = (ticketDetails: TicketDetails): Mutation => {
  const { accessesIds, tickets } = ticketDetails
  const ticketIds = tickets.map((t) => t.id)
  return {
    type: sanitizeFetchType(CREATE),
    resource: ResourceName.ACCESS_USINGS,
    payload: {
      data: { ticketIds, accessIds: accessesIds },
    } as CreateParams<AccessUsingsCreateParams>,
  }
}

const unblockMutation = ({ tickets }: TicketDetails): Mutation => {
  const activeAccessUsingIds = tickets
    .flatMap((t) => t.accessUsings)
    .filter((au) => au.active)
    .map((au) => au.id)
  return {
    type: sanitizeFetchType(UPDATE_MANY),
    resource: ResourceName.ACCESS_USINGS,
    payload: {
      ids: activeAccessUsingIds,
    } as UpdateManyParams,
  }
}

type State = {
  mutateQuery?: (ticketDetails: TicketDetails) => Mutation
} & ToggleButtonState

export const blockAccessButtonInitialState = {
  type: Access.DISABLED,
  text: 'resources.ticket-details.actions.blockAccessNA',
}

export const toggleAccessButtonReducer = (
  state: State,
  { type }: Action,
): State => {
  switch (type) {
    case Access.BLOCK: {
      return {
        type,
        text: 'resources.ticket-details.actions.blockAccess',
        mutateQuery: blockMutation,
      }
    }
    case Access.UNBLOCK: {
      return {
        type,
        text: 'resources.ticket-details.actions.unblockAccess',
        mutateQuery: unblockMutation,
      }
    }
    case Access.DISABLED: {
      return blockAccessButtonInitialState
    }
    default:
      throw new Error()
  }
}
