import {
  Box,
  Button,
  Collapse,
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'
import clsx from 'clsx'
import { ArrowDropDown, ArrowDropUp } from '@material-ui/icons'
import React, { ReactElement, useState } from 'react'
import { Show, ShowProps, useShowContext, useTranslate } from 'react-admin'
import { useSelector } from 'react-redux'
import {
  BlockOverallView,
  ObjectOverallView,
  TribuneOverallView,
} from '../../../core/stats/overall-view/overall-view'
import { ThemeState } from '../../../core/common/theme.reducer'
import { useThemeStyles } from '../../common/theme-styles'
import { useCommonStyles } from '../../common/common-styles'
import { ValueWithLabel } from '../../../core/common/types'

const ThemedTableRow = ({ children }: { children: ReactElement[] }) => {
  const isDarkMode = useSelector(
    (state: ThemeState) => state.theme.darkMode === 'dark',
  )
  const themeClasses = useThemeStyles({ isDarkMode })
  return <TableRow className={themeClasses.background}>{children}</TableRow>
}

const useStyles = makeStyles({
  noBottomBorder: {
    borderBottom: 'none',
  },
  quarterWidth: {
    width: '25%',
  },
})

const ViewTitle = (record: any) => {
  const {
    record: { eventName, date, id },
  } = record
  const t = useTranslate()
  const description = `${eventName} ${date.replace('T', ' ')} ${id}`
  return (
    <span>{`${t('resources.stats-overall-view.name')}: ${description}`}</span>
  )
}

const BlockView = ({
  capacity,
  entered,
  enteredAbo,
  enteredNormal,
  enteredInfix,
  enteredPoints,
  enteredService,
  name,
  sold,
  soldAbo,
  soldNormal,
  soldInfix,
  soldPoints,
  soldService,
}: BlockOverallView) => {
  const [open, setOpen] = useState(false)
  const classes = useStyles()
  const commonStyles = useCommonStyles()
  const t = useTranslate()

  const firstColumnEntries = [
    { label: 'resources.stats-overall-view.labels.normal', value: soldNormal },
    { label: 'resources.stats-overall-view.labels.abo', value: soldAbo },
    { label: 'resources.stats-overall-view.labels.infix', value: soldInfix },
    { label: 'resources.stats-overall-view.labels.points', value: soldPoints },
    {
      label: 'resources.stats-overall-view.labels.service',
      value: soldService,
    },
  ].map(
    ({ value, label }: ValueWithLabel) =>
      !!value && (
        <Box key={label} ml="71px">
          {t(label)}
        </Box>
      ),
  )

  const secondColumnEntries = [
    { label: enteredNormal, value: soldNormal },
    { label: enteredAbo, value: soldAbo },
    { label: enteredInfix, value: soldInfix },
    { label: enteredPoints, value: soldPoints },
    { label: enteredService, value: soldService },
  ].map(
    ({ value, label }: ValueWithLabel<number>, idx) =>
      !!value && (
        // eslint-disable-next-line react/no-array-index-key
        <Box key={idx} textAlign="center">
          {label}
        </Box>
      ),
  )

  const thirdColumnEntries = [
    { label: soldNormal, value: soldNormal },
    { label: soldAbo, value: soldAbo },
    { label: soldInfix, value: soldInfix },
    { label: soldPoints, value: soldPoints },
    { label: soldService, value: soldService },
  ].map(
    ({ value, label }: ValueWithLabel<number>, idx) =>
      !!value && (
        // eslint-disable-next-line react/no-array-index-key
        <Box key={idx} textAlign="center">
          {label}
        </Box>
      ),
  )

  return (
    <>
      <ThemedTableRow>
        <TableCell className={clsx({ [classes.noBottomBorder]: open })}>
          {sold > 0 && (
            <IconButton
              onClick={() => setOpen((v) => !v)}
              style={{ height: '40px', width: '40px', marginLeft: '30px' }}
            >
              {open ? <ArrowDropUp /> : <ArrowDropDown />}
            </IconButton>
          )}
          <span style={{ marginLeft: sold > 0 ? '2px' : '72px' }}>{name}</span>
        </TableCell>
        <TableCell
          className={clsx(
            { [classes.noBottomBorder]: open },
            commonStyles.textCentered,
          )}
        >
          {entered}
        </TableCell>
        <TableCell
          className={clsx(
            { [classes.noBottomBorder]: open },
            commonStyles.textCentered,
          )}
        >
          {sold}
        </TableCell>
        <TableCell
          className={clsx(
            { [classes.noBottomBorder]: open },
            commonStyles.textCentered,
          )}
        >
          {capacity}
        </TableCell>
      </ThemedTableRow>
      {open && (
        <ThemedTableRow>
          <TableCell>{firstColumnEntries}</TableCell>
          <TableCell>{secondColumnEntries}</TableCell>
          <TableCell>{thirdColumnEntries}</TableCell>
          <TableCell />
        </ThemedTableRow>
      )}
    </>
  )
}

const TribuneView = (tribune: TribuneOverallView) => {
  const { capacity, entered, name, sold, blocks } = tribune
  const [open, setOpen] = useState(false)
  const commonStyles = useCommonStyles()
  return (
    <>
      {blocks.length > 0 && capacity > 0 && (
        <>
          <TableRow>
            <TableCell>
              <IconButton
                onClick={() => setOpen((v) => !v)}
                style={{ height: '40px', width: '40px' }}
              >
                {open ? <ArrowDropUp /> : <ArrowDropDown />}
              </IconButton>
              <span style={{ marginLeft: '2px' }}>{name}</span>
            </TableCell>
            <TableCell className={commonStyles.textCentered}>
              {entered}
            </TableCell>
            <TableCell className={commonStyles.textCentered}>{sold}</TableCell>
            <TableCell className={commonStyles.textCentered}>
              {capacity}
            </TableCell>
          </TableRow>
          {open &&
            blocks.map((block) => <BlockView {...block} key={block.id} />)}
        </>
      )}
    </>
  )
}

const ObjectView = (objectOverallView: ObjectOverallView) => {
  const { name, tribunes } = objectOverallView
  const [open, setOpen] = useState(true)
  const classes = useStyles()
  const commonStyles = useCommonStyles()
  const t = useTranslate()

  const quantityColumnLabels = [
    'resources.stats-overall-view.labels.entries',
    'resources.stats-overall-view.labels.sold',
    'resources.stats-overall-view.labels.capacity',
  ].map((label) => (
    <TableCell
      key={label}
      className={clsx(classes.quarterWidth, commonStyles.textCentered)}
    >
      {t(label)}
    </TableCell>
  ))

  const summaryRow = ({ entered, sold, capacity }: ObjectOverallView) => (
    <TableRow style={{ height: '40px' }}>
      <TableCell>
        <Box ml="2px">{t('resources.stats-overall-view.labels.total')}</Box>
      </TableCell>
      {[entered, sold, capacity].map((value, idx) => (
        // eslint-disable-next-line react/no-array-index-key
        <TableCell key={idx} className={commonStyles.textCentered}>
          {value}
        </TableCell>
      ))}
    </TableRow>
  )

  return (
    <Box
      style={{ margin: '5px', borderRadius: '5px', boxShadow: '0px 0px 2px' }}
    >
      <Button style={{ width: '100%' }} onClick={() => setOpen((v) => !v)}>
        {name}
      </Button>
      <Collapse in={open}>
        <TableContainer>
          <Table padding="none">
            <TableHead>
              <TableRow>
                <TableCell style={{ width: '25%' }}>
                  <div style={{ marginLeft: '2px' }}>
                    {t('resources.stats-overall-view.labels.tribune')}
                  </div>
                </TableCell>
                {quantityColumnLabels}
              </TableRow>
            </TableHead>
            <TableBody>
              {tribunes.map((tribune) => (
                <TribuneView {...tribune} key={tribune.id} />
              ))}
              {summaryRow(objectOverallView)}
            </TableBody>
          </Table>
        </TableContainer>
      </Collapse>
      {!open && (
        <Table padding="none">
          <TableHead>
            <TableRow>
              <TableCell style={{ width: '25%' }} />
              {quantityColumnLabels}
            </TableRow>
          </TableHead>
          <TableBody>{summaryRow(objectOverallView)}</TableBody>
        </Table>
      )}
    </Box>
  )
}

const StatsView = () => {
  const { record } = useShowContext()

  return (
    <>
      {record?.objects.map((object: ObjectOverallView) => (
        <ObjectView {...object} key={record.id} />
      ))}
    </>
  )
}

export const OverallView = (props: ShowProps) => (
  <Show title={<ViewTitle />} {...props}>
    <StatsView />
  </Show>
)
