import { ExtendedLogFilterValues } from './extended-log'

const mapping: Partial<
  Record<
    keyof ExtendedLogFilterValues,
    (lambdaParameter: string, value: string | number) => string
  >
> = {
  ticketCode: (lambdaParameter, value) =>
    `${lambdaParameter}.TicketCode.Contains("${value}")`,
  bookingId: (lambdaParameter, value) =>
    `${lambdaParameter}.BookingId.ToString().Contains("${value}")`,
  date: (lambdaParameter, value) =>
    `${lambdaParameter}.Date > DateTimeOffset.FromUnixTimeMilliseconds(${
      value.toString().split(';')[0]
    }).DateTime && l.Date < DateTimeOffset.FromUnixTimeMilliseconds(${
      value.toString().split(';')[1]
    }).DateTime`,
  deviceName: (lambdaParameter, value) =>
    `${lambdaParameter}.Device.Name.Contains("${value}")`,
  text: (lambdaParameter, value) =>
    `${lambdaParameter}.Data.Text.Contains("${value}")`,
  event: (lambdaParameter, value) =>
    `${lambdaParameter}.Booking.Event.Name.Contains("${value}")`,
}

export const getFilterMapper = (key: keyof ExtendedLogFilterValues) =>
  mapping[key] as (lambdaParameter: string, value: string | number) => string
