import { DataProvider, GetListResult } from 'react-admin'
import { getByConditions } from '../common/fetch.utils'
import { TICKETS_URL } from '../api-urls'
import { TicketCodeFilter } from '../common/ticket-code.filter'
import { TicketDto } from '../dto/ticket.dto'

const provider = {
  getList: async (
    resource: string,
    { filter }: TicketCodeFilter,
  ): Promise<GetListResult<TicketDto>> => {
    const filterByTicketCodeCondition = `t=>t.TicketCode=="${filter.ticketCode}"`
    const tickets = await getByConditions<TicketDto>(
      TICKETS_URL,
      filterByTicketCodeCondition,
    )
    return Promise.resolve({
      data: tickets,
      total: tickets.length,
    })
  },
} as DataProvider

export default provider
