import { ExtendedLog } from './extended-log'

const mapping: Partial<Record<keyof ExtendedLog, string>> = {
  id: 'l=>l.Id',
  date: 'l=>l.Date',
  deviceName: 'l=>l.Device.Name',
  ticketCode: 'l=>l.TicketCode',
  bookingId: 'l=>l.BookingId',
}

export const mapSortLogParam = (key: keyof ExtendedLog) => mapping[key]
