import { Box, Card, Divider, Typography } from '@material-ui/core'
import * as React from 'react'
import {
  Datagrid,
  List,
  ListProps,
  TextField,
  useListContext,
  useTranslate,
} from 'react-admin'
import { useLocation } from 'react-router-dom'
import { ResourceName } from '../../core/ResourceName'
import { FindUserByTicket } from '../common/FindUserByTicket'
import { MaskField } from '../common/MaskField'

const TicketInfoGrid = () => {
  const {
    loading,
    filterValues: { ticketCode },
  } = useListContext()
  const t = useTranslate()
  return (
    <>
      <Box m={2}>
        <Typography variant="h5" paragraph>
          {t('resources.ticket-info.search-list-title', { ticketCode })}
        </Typography>
      </Box>
      <Divider />
      <Datagrid
        loaded={!loading}
        rowClick={(id) => `${ResourceName.TICKET_DETAILS}/${id}/show`}
      >
        <TextField source="firstname" sortable={false} />
        <TextField source="lastname" sortable={false} />
        <MaskField source="pesel" sortable={false} />
        <TextField source="ticketCode" sortable={false} />
        <TextField source="eventName" sortable={false} />
        <TextField source="id" sortable={false} />
      </Datagrid>
    </>
  )
}

export const TicketInfoList = (props: ListProps) => {
  const { search } = useLocation()
  return (
    <>
      <Card style={{ marginTop: '2%' }}>
        <FindUserByTicket titleCentered />
      </Card>
      {!!search && (
        <List actions={false} pagination={false} {...props}>
          <TicketInfoGrid />
        </List>
      )}
    </>
  )
}
