import {
  CreateParams,
  CreateResult,
  DataProvider,
  UpdateManyParams,
  UpdateManyResult,
} from 'react-admin'
import { AccessUsingsCreateParams } from './access-usings-create.params'
import { AccessUsingDto } from '../dto/access-using.dto'
import { DeviceDirection } from '../enum/DeviceDirection'
import { DateTime } from '../common/date-time'
import { patch, post } from '../common/fetch.utils'
import { ACCESS_USINGS } from '../api-urls'

type CreateAccessUsingRequest = Array<
  Omit<AccessUsingDto, 'id' | 'verifierDeviceId'> & { verifierDeviceId: null }
>
type UpdateAccessUsingRequest = Pick<AccessUsingDto, 'id'> & { active: false }

const provider = {
  updateMany: async (
    resource,
    { ids }: UpdateManyParams,
  ): Promise<UpdateManyResult> => {
    const pendingUpdates = ids
      .map((auId) => ({ id: auId, active: false } as UpdateAccessUsingRequest))
      .map((req) =>
        patch<UpdateAccessUsingRequest, AccessUsingDto>(ACCESS_USINGS, req),
      )
    const accessUsings = await Promise.all(pendingUpdates)
    return Promise.resolve({
      data: accessUsings.map((au) => au.id),
    })
  },
  create: async (
    resource,
    { data }: CreateParams<AccessUsingsCreateParams>,
  ): Promise<CreateResult> => {
    const { ticketIds, accessIds } = data
    const request: CreateAccessUsingRequest = accessIds.flatMap((accessId) =>
      ticketIds.map((ticketId) => ({
        date: new Date().toISOString() as DateTime,
        direction: DeviceDirection.IN,
        active: true,
        accessId,
        ticketId,
        verifierDeviceId: null,
      })),
    )
    await post<CreateAccessUsingRequest, AccessUsingDto[]>(
      ACCESS_USINGS,
      request,
    )
    return Promise.resolve({
      data: { id: 0 }, // need to return something
    })
  },
} as DataProvider

export default provider
