import { CustomerDto } from '../dto/customer.dto'
import { EventDto } from '../dto/event.dto'
import { TicketDto } from '../dto/ticket.dto'
import { VariantDto } from '../dto/variant.dto'
import { TicketDetails } from './ticket-details'
import { BookingDto } from '../dto/booking.dto'
import { AccessUsingDto } from '../dto/access-using.dto'
import { Ticket } from './ticket'
import { toMapById } from '../common/mapper.utils'

const mapToTicket = (
  { id, ticketCode, ticketType, createdAt, accessUsingsIds }: TicketDto,
  accessUsingById: Map<number, AccessUsingDto>,
): Ticket => ({
  id,
  ticketCode,
  ticketType,
  createdAt,
  accessUsings: accessUsingsIds.map(
    (auId) => accessUsingById.get(auId) as AccessUsingDto,
  ),
})

export const mapToTicketDetails = (
  {
    id: bookingId,
    disabled: bookingDisabled,
    type: bookingType,
    activeFrom,
    activeTo,
    createdAt,
    modifiedAt,
  }: BookingDto,
  { firstname, lastname, pesel }: CustomerDto,
  { name: eventName }: EventDto,
  { name: variantName, accessesIds }: VariantDto,
  tickets: TicketDto[],
  accessUsings: AccessUsingDto[],
): TicketDetails => {
  const accessUsingById = toMapById(accessUsings)
  return {
    id: bookingId,
    firstname,
    lastname,
    eventName,
    pesel,
    variantName,
    accessesIds,
    bookingType,
    activeFrom,
    activeTo,
    tickets: tickets.map((t) => mapToTicket(t, accessUsingById)),
    createdAt,
    modifiedAt,
    bookingDisabled,
  }
}
