import { Access, Action, ToggleButtonState } from './toggle-button'
import { BookingUpdateRequest } from '../bookings/booking-update.request'
import { BookingDisabledOption } from '../enum/BookingDisabledOption'

type State = {
  mutateParam?: BookingUpdateRequest['disabled']
} & ToggleButtonState

export const blockTicketButtonInitialState = {
  type: Access.DISABLED,
  text: 'resources.ticket-details.actions.blockTicketNA',
}

export const toggleTicketButtonReducer = (
  state: State,
  { type }: Action,
): State => {
  switch (type) {
    case Access.BLOCK: {
      return {
        type,
        text: 'resources.ticket-details.actions.blockTicket',
        mutateParam: BookingDisabledOption.ADMIN,
      }
    }
    case Access.UNBLOCK: {
      return {
        type,
        text: 'resources.ticket-details.actions.unblockTicket',
        mutateParam: null,
      }
    }
    case Access.DISABLED: {
      return blockTicketButtonInitialState
    }
    default:
      throw new Error()
  }
}
