import { TicketType } from '../enum/TicketType'
import { BookingType } from '../enum/BookingType'

const bookingTypeMapping: Record<BookingType, string> = {
  [BookingType.NORMAL]: 'resources.enums.bookingType.normal',
  [BookingType.ABO]: 'resources.enums.bookingType.abo',
  [BookingType.INFIX]: 'resources.enums.bookingType.infix',
  [BookingType.POINTS]: 'resources.enums.bookingType.points',
  [BookingType.SERVICE]: 'resources.enums.bookingType.service',
}

const ticketTypeMapping: Record<TicketType, string> = {
  [TicketType.K]: 'resources.enums.ticketType.k',
  [TicketType.M]: 'resources.enums.ticketType.m',
  [TicketType.Z]: 'resources.enums.ticketType.z',
  [TicketType.N]: 'resources.enums.ticketType.n',
  [TicketType.P]: 'resources.enums.ticketType.p',
  [TicketType.U]: 'resources.enums.ticketType.u',
  [TicketType.R]: 'resources.enums.ticketType.r',
}

export const mapFromBookingType = (bookingType: BookingType): string =>
  bookingTypeMapping[bookingType]

export const mapFromTicketType = (ticketType: TicketType): string =>
  ticketTypeMapping[ticketType]
