import { stringify } from 'querystring'
import * as React from 'react'
import { useCallback, useState } from 'react'
import clsx from 'clsx'
import {
  GET_LIST,
  GetListResult,
  sanitizeFetchType,
  useMutation,
  useNotify,
  useTranslate,
} from 'react-admin'
import { CardContent, CardHeader } from '@material-ui/core'
import { useHistory } from 'react-router-dom'
import { TicketCodeFilter } from '../../core/common/ticket-code.filter'
import { TicketDto } from '../../core/dto/ticket.dto'
import { ResourceName } from '../../core/ResourceName'
import SearchInput from './SearchInput'
import { useCommonStyles } from './common-styles'

type FindUserByTicketProps = Readonly<{ titleCentered?: boolean }>

export const FindUserByTicket = ({
  titleCentered = false,
}: FindUserByTicketProps) => {
  const history = useHistory()
  const notify = useNotify()
  const [error, setError] = useState('')
  const [mutate, { loading }] = useMutation()
  const commonClasses = useCommonStyles()
  const t = useTranslate()

  const fetchTickets = useCallback(
    (ticketCode: string) =>
      mutate(
        {
          type: sanitizeFetchType(GET_LIST),
          resource: ResourceName.TICKETS,
          payload: { filter: { ticketCode } } as TicketCodeFilter,
        },
        { returnPromise: true },
      ) as Promise<GetListResult<TicketDto>>,
    [mutate],
  )

  const onValue = useCallback(
    async (ticketCode: string) => {
      setError('')
      try {
        const {
          total,
          data: [onlyTicket],
        } = await fetchTickets(ticketCode)
        if (!total) {
          setError('common.find-user-by-ticket.error')
        } else if (total === 1) {
          history.push({
            pathname: `/${ResourceName.TICKET_DETAILS}/${onlyTicket.bookingId}/show`,
          })
        } else {
          history.push({
            pathname: `/${ResourceName.TICKET_INFO}`,
            search: stringify({
              filter: JSON.stringify({
                ticketCode,
              } as TicketCodeFilter['filter']),
            }),
          })
        }
      } catch (err) {
        notify((err as Error)?.message, 'error')
      }
    },
    [fetchTickets, history, notify],
  )

  return (
    <>
      <CardHeader
        className={clsx({ [commonClasses.textCentered]: titleCentered })}
        title={t('common.find-user-by-ticket.title')}
      />
      <CardContent>
        <SearchInput
          label="common.find-user-by-ticket.input-label"
          onValue={onValue}
          disabled={loading}
          error={error}
        />
      </CardContent>
    </>
  )
}
