import React, { CSSProperties, useEffect, useState } from 'react'
import {
  Collapse,
  Grid,
  Hidden,
  IconButton,
  TableCell,
  TableRow,
} from '@material-ui/core'
import { DateField, TextField, useTranslate } from 'react-admin'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown'
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp'
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { ResourceName } from '../../core/ResourceName'
import { BookingButton, CenterGrid, NoPaddingCell } from './LogsComponents'
import { ExtendedLog } from '../../core/logs/extended-log'
import { ThemeState } from '../../core/common/theme.reducer'
import { getContrastColor } from '../../core/common/visual.utils'

interface LogRowProps {
  readonly compactMode: boolean
  readonly noBooking: boolean
  readonly data: ExtendedLog
}

export const LogRow = (props: LogRowProps) => {
  const { data, compactMode, noBooking } = props
  const [open, setOpen] = useState(false)
  const history = useHistory()
  const isDarkMode = useSelector(
    (state: ThemeState) => state.theme.darkMode === 'dark',
  )
  const t = useTranslate()

  useEffect(() => {
    setOpen(false)
  }, [data])

  const defineStyle = () => {
    if (
      (!isDarkMode && data.color === 'transparent') ||
      getContrastColor(data.color) === 'black'
    ) {
      return { color: 'black', fontWeight: 'normal' } as CSSProperties
    }
    return { color: 'white', fontWeight: 'bold' } as CSSProperties
  }

  return (
    <>
      <TableRow style={{ backgroundColor: data.color }}>
        <Hidden mdUp={!compactMode}>
          <TableCell>
            <IconButton onClick={() => setOpen((v) => !v)}>
              {open ? (
                <ArrowDropUpIcon style={defineStyle()} />
              ) : (
                <ArrowDropDownIcon style={defineStyle()} />
              )}
            </IconButton>
          </TableCell>
        </Hidden>
        <TableCell>
          <DateField
            record={data}
            source="date"
            showTime
            style={defineStyle()}
          />
        </TableCell>
        <TableCell>
          <TextField record={data} source="deviceName" style={defineStyle()} />
        </TableCell>
        <TableCell>
          <TextField record={data} source="ticketCode" style={defineStyle()} />
        </TableCell>
        <Hidden smDown xlDown={compactMode}>
          <TableCell>
            <TextField
              record={data}
              source="text"
              sortable={false}
              style={defineStyle()}
            />
          </TableCell>
        </Hidden>
        <Hidden smDown xlDown={compactMode || noBooking}>
          <TableCell>
            {data.bookingId == null ? (
              <div />
            ) : (
              <BookingButton
                onClick={() =>
                  history.push({
                    pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show`,
                  })
                }
              >
                {data.bookingId}
              </BookingButton>
            )}
          </TableCell>
        </Hidden>
      </TableRow>
      <Hidden mdUp={!compactMode}>
        <TableRow>
          <NoPaddingCell colSpan={4}>
            <Collapse in={open}>
              <Grid
                container
                style={{ marginTop: '1%', marginBottom: '1%' }}
                item
              >
                <Hidden xlDown={noBooking}>
                  <CenterGrid item xs={5}>
                    {`${t('resources.logs.fields.bookingId')}:`}
                  </CenterGrid>
                  <CenterGrid item xs={7}>
                    {data.bookingId == null ? (
                      <div />
                    ) : (
                      <BookingButton
                        onClick={() =>
                          history.push({
                            pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show`,
                          })
                        }
                      >
                        {data.bookingId}
                      </BookingButton>
                    )}
                  </CenterGrid>
                  <hr
                    style={{
                      width: '90%',
                      marginTop: '1%',
                      marginBottom: '1%',
                    }}
                  />
                </Hidden>
                <CenterGrid item xs={5}>
                  {`${t('resources.logs.fields.description')}:`}
                </CenterGrid>
                <CenterGrid item xs={7}>
                  {data.text}
                </CenterGrid>
              </Grid>
            </Collapse>
          </NoPaddingCell>
        </TableRow>
      </Hidden>
    </>
  )
}
