import { DataProvider, GetListResult } from "react-admin"
import { EVENTS_URL } from '../../api-urls'
import { getByConditions } from '../../common/fetch.utils'
import { ALL_RESULTS } from "../../common/filter.values"
import { NameFilter } from "../../common/name.filter"
import { EventDto } from "../../dto/event.dto"
import { EventOverallView } from "./event-overall-view"
import { mapToEventOverallView } from "./overall-view-list.mapper"

const provider = {
  getList: async (
    resource: string,
    { filter }: NameFilter,
  ): Promise<GetListResult<EventOverallView>> => {
    const events = await getByConditions<EventDto>(EVENTS_URL, filter.name ? `e=>e.Name.Contains("${filter.name}")` : ALL_RESULTS)

    const overallViewList = events.map((event) => mapToEventOverallView(event))

    return {
      data: overallViewList,
      total: overallViewList.length,
    }
  },
} as DataProvider

export default provider