import React from 'react'
import {
  Hidden,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'
import { useListContext, useListSortContext, useTranslate } from 'react-admin'
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import { ExtendedLog } from '../../core/logs/extended-log'
import { FullWidthButton } from './LogsComponents'
import { LogRow } from './LogRow'

interface LogGridProps {
  readonly compactMode: boolean
  readonly noBooking: boolean
}

export const LogGrid = (props: LogGridProps) => {
  const { compactMode, noBooking } = props
  const { ids, data } = useListContext()
  const { currentSort, setSort } = useListSortContext()
  const t = useTranslate()

  const inverseOrder = (order: string) => (order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (sortBy: string) => {
    setSort(
      sortBy,
      sortBy === currentSort.field ? inverseOrder(currentSort.order) : 'DESC',
    )
  }

  const HeaderCell = (name: string, display: string) => (
    <>
      {currentSort.field === name ? (
        <FullWidthButton
          startIcon={
            currentSort.order === 'ASC' ? (
              <ArrowUpwardIcon />
            ) : (
              <ArrowDownwardIcon />
            )
          }
        >
          {t(display)}
        </FullWidthButton>
      ) : (
        <FullWidthButton>{t(display)}</FullWidthButton>
      )}
    </>
  )

  return (
    <TableContainer component={Paper}>
      <Table
        aria-label="a dense table"
        size="small"
        padding={compactMode ? 'none' : 'default'}
      >
        <TableHead>
          <TableRow>
            <Hidden mdUp={!compactMode}>
              <TableCell style={{ width: '3%' }} />
            </Hidden>
            <TableCell
              onClick={() => handleSortChange('date')}
              style={{ width: '10%' }}
            >
              {HeaderCell('date', 'resources.logs.fields.date')}
            </TableCell>
            <TableCell
              onClick={() => handleSortChange('deviceName')}
              style={{ width: '15%' }}
            >
              {HeaderCell('deviceName', 'resources.logs.fields.deviceName')}
            </TableCell>
            <TableCell
              onClick={() => handleSortChange('ticketCode')}
              style={{ width: '25%' }}
            >
              {HeaderCell('ticketCode', 'resources.logs.fields.ticketCode')}
            </TableCell>
            <Hidden smDown xlDown={compactMode}>
              <TableCell style={{ textAlign: 'center', width: '25%' }}>
                {t('resources.logs.fields.description').toUpperCase()}
              </TableCell>
            </Hidden>
            <Hidden smDown xlDown={compactMode || noBooking}>
              <TableCell
                onClick={() => handleSortChange('bookingId')}
                style={{ width: '15%' }}
              >
                {HeaderCell('bookingId', 'resources.logs.fields.bookingId')}
              </TableCell>
            </Hidden>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <LogRow
              key={id}
              data={data[id] as ExtendedLog}
              compactMode={compactMode}
              noBooking={noBooking}
            />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}
